import {NgModule, ModuleWithProviders} from '@angular/core';
import {CommonModule} from '@angular/common';
import {VdlTopBar, VdlTopBarLogo, VdlTopBarTitle, VdlTopBarActions} from './top-bar';
import {VdlInputModule} from '../input/index';
import {VdlIconModule} from '../icon/icon';
import {VdlButtonModule} from '../button/button';

export * from './top-bar';

@NgModule({
  imports: [
    CommonModule,
    VdlInputModule,
    VdlIconModule,
    VdlButtonModule
  ],
  exports: [
    VdlTopBar,
    VdlTopBarTitle,
    VdlTopBarLogo,
    VdlTopBarActions,
  ],
  declarations: [
    VdlTopBar,
    VdlTopBarTitle,
    VdlTopBarLogo,
    VdlTopBarActions
  ],
})
export class VdlTopBarModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: VdlTopBarModule,
      providers: []
    };
  }
}
